<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="levelTwoLabel">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small">新增二级标签</el-button>
            <div>
                <el-input
                        placeholder="请输入二级标签名称"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="选择一级标签"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="状态"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="操作人"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="$refs.pagination.Refresh()">查询</el-button>
                <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="一级标签名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="二级标签名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="状态">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="最近操作人">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="280">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="warning" size="small"
                               >禁用</el-button>
                    <el-button @click="handleClick(scope.row)" type="primary" size="small"
                               >修改</el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                               >删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins:[paginationMixin],
        data() {
            return {
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
            tableAction() {
                return this.$createElement('HelpHint', {
                    props: {
                        content: '查看文章 / 编辑文章'
                    }
                }, '操作');
            },

        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
